import React, { Component } from 'react'
import Header from './Header';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import localizationUA from '../localization/UA/HomeUA';
import localizationRU from '../localization/RU/HomeRU';

class Presentation extends Component {
  render() {
    return (
        <div>
          <Header/>
          <iframe src={this.props.location.state.link} style={{width: '100%', height: '100vh'}} frameborder="0"></iframe>
          {
            this.props.lang.isRu ? localizationRU.footer() : localizationUA.footer()
          }
        </div>
    )
  }
}

const mapStateToProps = (state) => {
  return { 
    lang: state.lang,
  };
};

export default connect(mapStateToProps)(Presentation);
