import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { Provider } from 'react-redux';
import './index.css';
import App from './App';
import Test from './Components/Test';
import AdminContainer from './Components/Admin/AdminContainer';
import Login from './Components/Admin/Login';
import PrivateRoute from './Components/Admin/PrivateRoute';
import store from './store';
import * as serviceWorker from './serviceWorker';
import AboutUs from './Components/AboutUs';
import News from './Components/News';
import Contacts from './Components/Contacts';
import Portfolio from './Components/Portfolio';
import Form from './Components/Form';
import NewsView from './Components/NewsView';
import Error from './Components/Error';
import Presentation from './Components/Presentation';
import CancelSubscription from './Components/CancelSubscription';
import SubscribeSuccess from './Components/SubscribeSuccess';

ReactDOM.render(
  <Provider store={store}>
    <BrowserRouter>
      <Switch>
        <Route path='/about-us' component={AboutUs}/>
        <Route path='/ua/about-us' component={AboutUs}/>
        <Route path="/login" component={Login}/>
        <PrivateRoute path="/admin" component={AdminContainer}/>
        <Route path='/news' component={News}/>
        <Route path='/ua/news' component={News}/>
        <Route path='/contacts' component={Contacts}/>
        <Route path='/ua/contacts' component={Contacts}/>
        <Route path='/portfolio' component={Portfolio}/>
        <Route path='/ua/portfolio' component={Portfolio}/>
        <Route path='/form' component={Form}/>
        <Route path='/ua/form' component={Form}/>
        <Route path='/post/:url' component={NewsView}/>
        <Route path='/ua/post/:url' component={NewsView}/>
        <Route path='/error' component={Error}/>
        <Route path='/news-view' component={NewsView}/>
        <Route path='/ua/news-view' component={NewsView}/>
        <Route path='/presentation' component={Presentation}/>
        <Route path='/ua/presentation' component={Presentation}/>
        <Route path='/cancel-subscription' component={CancelSubscription}/>
        <Route path='/newsletter/verify' component={SubscribeSuccess}/>
        <Route path='/newsletter/unsubscribe' component={CancelSubscription}/>
        <Route exact path='/' component={App}/>
        <Route exact path='/ua/' component={App}/>
        <Route component={Error}/>
      </Switch>
    </BrowserRouter>
  </Provider>,
  document.getElementById('root')
 );

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
